/*

Buttons

*/
.btn {
  position: relative;
  display: inline-block;
  outline: none;
  text-decoration: none;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0;
  padding: 16px 20px;
  vertical-align: middle;
  white-space: nowrap;
  border-radius: 2px;
  height: 52px;
  min-width: 196px;
  color: #ffffff !important;
  -webkit-appearance: none;
  background: #82bac3;
  user-select: none;
  backface-visibility: hidden;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  transition: all 0.3s;
  cursor: pointer;
  border: 2px solid #82bac3;
}

.btn:after {
  content: '\e800';
  display: inline-block;
  vertical-align: middle;
  margin-left: 15px;
  position: relative;
  top: -1px;
  font-size: 12px;
  font-family: fontello;
}

@media (max-width: 1250px) {
  .btn {
    padding: 17px 20px;
  }
}

.btn:hover {
  color: #585858 !important;
  border: 2px solid #585858;
  background: #ffffff !important;
}

.btn::-moz-focus-inner {
  padding: 0 !important;
  border: 0 none !important;
}

.btn:active {
  text-decoration: none;
}

.btn[disabled], .btn.disabled {
  background-color: #ebebeb;
  color: #888 !important;
  border-color: inherit;
  border-color: currentColor;
  opacity: 1;
  cursor: default;
  cursor: not-allowed;
}

.btn + .btn {
  margin-left: 30px;
}

.btn > input[type='file'] {
  opacity: 0;
  filter: alpha(opacity=0);
  font-size: 120px;
  position: absolute;
  top: -20px;
  right: -10px;
  vertical-align: top;
  z-index: 3;
  cursor: pointer;
}

.btn--wide {
  min-width: 300px;
}

.btn--small {
  min-width: 125px;
  height: 37px;
  padding: 12px 12px;
  font-size: 11px;
}

.btn--black {
  background: #585858 !important;
  color: #ffffff !important;
  border: 2px solid #585858;
}

.btn--black:hover {
  color: #585858 !important;
  background: #ffffff !important;
}

.btn--brown {
  background: #c08860 !important;
  color: #ffffff !important;
  border: 2px solid #c08860;
}

.btn--brown:hover {
  color: #c08860 !important;
  background: #ffffff !important;
  border: 2px solid #c08860;
}

.btn--gray {
  background: #b4b0b2 !important;
  color: #ffffff !important;
  border: 2px solid #b4b0b2;
}

.btn--gray:hover {
  color: #b4b0b2 !important;
  background: #ffffff !important;
  border: 2px solid #b4b0b2;
}

.btn--no-arr:after {
  content: '';
}

.btn--ok:before {
  content: '\e801';
  display: inline-block;
  vertical-align: middle;
  margin-right: 15px;
  color: #82bac3;
  position: relative;
  top: -2px;
  font-size: 16px;
  font-family: fontello;
}

.btn--white {
  background: #ffffff !important;
  color: #585858 !important;
  border-color: .btn--white color1 !important;
}

.btn--white:hover, .btn--white:active, .btn--white:focus, .btn--white .touched {
  background: #ffffff !important;
}

.btn--white-border {
  background: transparent !important;
  color: #ffffff !important;
  border: 1px solid #ffffff !important;
}

.btn--white-border:hover, .btn--white-border:active, .btn--white-border:focus, .btn--white-border .touched {
  background: transparent !important;
}

.btn--menu {
  display: block;
  width: 100%;
  height: 51px;
  border-radius: 0;
  padding: 17px 26px;
}

.btn--large {
  font-size: 18px;
  height: 92px;
  min-width: 408px;
  padding: 40px 45px;
  font-weight: 600;
  color: #82bac3 !important;
}

@media (max-width: 480px) {
  .btn--large {
    min-width: 95%;
  }
}

.btn--link {
  padding: 0;
  min-width: 10px;
  color: #000000 !important;
  background: none !important;
  height: auto;
  border: none;
}

.btn--no-hover:hover {
  color: inherit !important;
  background: inherit !important;
  border-color: inherit;
  border-color: currentColor;
}

.btn--fullwidth {
  width: 100%;
  margin-top: 20px;
}

.btn--fullwidth:first-child {
  margin-top: 0;
}

