.home-slides-nav__prev, .home-slides-nav__next {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  border: 3px solid rgba(0, 0, 0, 0.5);
  border-right: none;
  border-top: none;
  transform: rotate(45deg);
  cursor: pointer;
  left: 2%;
  top: 60%;
  z-index: 99;
  background: transparent;
  margin-top: -12px;
}

@media (max-width: 1070px) {
  .home-slides-nav__prev, .home-slides-nav__next {
    top: 50%;
  }
}

@media (max-width: 780px) {
  .home-slides-nav__prev, .home-slides-nav__next {
    display: none;
  }
}

.home-slides-nav__next {
  transform: rotate(-135deg);
  left: inherit;
  right: 2%;
}

.home-slides-nav button {
  position: relative;
  display: inline-block;
  padding: 0.35em;
  margin: 0;
  font-family: "Roboto", sans-serif;
  appearance: none;
  background: transparent;
  border: 0;
  overflow-x: hidden;
  transition: color 0.5s ease;
}

.home-slides-nav button:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 0;
  background: #000000;
  transition: width 0.4s ease;
}

.home-slides-nav button:hover {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.75);
  transition: color 0.5s ease;
}

.home-slides-nav button:hover:after {
  width: 100%;
  transition: width 0.4s ease;
}

.home-slides-nav button:focus {
  outline: 0;
}

.is-sliding .home-slides-nav {
  pointer-events: none;
}

.home-slides {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
  background: #fff;
  transition: background 1s cubic-bezier(0.99, 1, 0.92, 1);
}

.home-slides-wrap {
  height: 606px;
}

@media (max-width: 780px) {
  .home-slides-wrap {
    height: 506px;
  }
}

@media (max-width: 480px) {
  .home-slides-wrap {
    height: 406px;
  }
}

.home-slide {
  z-index: -1;
  padding: 0;
  position: absolute;
  width: 88%;
  height: 100%;
  right: 0;
  transition: z-index 1s ease;
}

@media (max-width: 1250px) {
  .home-slide {
    width: 100%;
  }
}

.home-slide.is-active {
  z-index: 19;
  transition: z-index 1s ease;
}

.home-slide__content {
  position: relative;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  top: 0%;
}

@media (min-width: 1250px) {
  .home-slide__content {
    height: 100%;
    width: 100%;
    top: 0%;
  }
}

.home-slide__header {
  z-index: 9;
  position: relative;
  height: 100%;
  display: flex;
  align-items: flex-end;
  overflow-y: hidden;
  transform: translateX(-13%);
}

@media (max-width: 1250px) {
  .home-slide__header {
    transform: translateX(0);
  }
}

.home-slide__title {
  font-family: "BebasNeue", sans-serif;
  font-size: 2.5em;
  color: #000000;
  overflow-y: hidden;
  width: 504px;
  background: rgba(255, 255, 255, 0.75);
  padding: 60px 60px 60px 0;
}

@media (max-width: 1250px) {
  .home-slide__title {
    padding: 60px 60px 60px 5px;
  }
}

@media (max-width: 1070px) {
  .home-slide__title {
    width: 86%;
    margin: auto;
    padding: 60px 60px 60px 40px;
  }
}

@media (max-width: 780px) {
  .home-slide__title {
    width: 96%;
    padding: 20px;
  }
  .home-slide__title .text-right {
    text-align: left;
  }
}

@media (max-width: 480px) {
  .home-slide__title {
    padding: 20px 15px;
  }
}

.home-slide__title .title-header {
  font-weight: 300;
  font-size: 32px;
  display: block;
  margin-bottom: 25px;
  transform: translate3d(0, 40%, 0);
  opacity: 0;
  transition: transform 0.4s ease, opacity 0.8s ease;
}

@media (max-width: 780px) {
  .home-slide__title .title-header {
    font-size: 26px;
  }
}

@media (max-width: 480px) {
  .home-slide__title .title-header {
    font-size: 24px;
  }
}

.home-slide__title .title-header span {
  font-size: 51px;
  font-weight: 400;
  line-height: 1.2;
}

@media (max-width: 780px) {
  .home-slide__title .title-header span {
    font-size: 40px;
  }
}

@media (max-width: 480px) {
  .home-slide__title .title-header span {
    font-size: 32px;
  }
}

.is-active .home-slide__title .title-header {
  transform: translate3d(0, 0%, 0);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.77, 0, 0.175, 1), opacity 0.5s ease;
  transition-delay: 0.4s;
}

.home-slide__title .title-descr {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  line-height: 1.8;
  text-align: right;
  display: block;
  font-weight: 300;
  padding-right: 25px;
  margin-bottom: 50px;
  position: relative;
  transform: translate3d(0, 40%, 0);
  opacity: 0;
  transition: transform 0.8s ease, opacity 0.8s ease;
}

@media (max-width: 480px) {
  .home-slide__title .title-descr {
    text-align: left;
    padding: 0;
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 30px;
  }
}

.is-active .home-slide__title .title-descr {
  transform: translate3d(0, 0%, 0);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.77, 0, 0.175, 1), opacity 0.5s ease;
  transition-delay: 0.8s;
}

.home-slide__title .title-descr:before {
  content: "";
  width: 2px;
  height: 140px;
  background: #82bac3;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  transform: translateY(-13%);
}

@media (max-width: 480px) {
  .home-slide__title .title-descr:before {
    display: none;
  }
}

.home-slide__title .title-descr:after {
  content: "";
  width: 6px;
  height: 6px;
  background: #82bac3;
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  transform: translate(2px, -40px);
  border-radius: 6px;
  opacity: 0;
  transition: opacity 0.8s ease;
}

@media (max-width: 480px) {
  .home-slide__title .title-descr:after {
    display: none;
  }
}

.is-active .home-slide__title .title-descr:after {
  opacity: 1;
  transition: opacity 0.5s ease;
  transition-delay: 1.5s;
}

.home-slide__title .btn {
  opacity: 0;
  transition: opacity 0.8s ease;
}

.is-active .home-slide__title .btn {
  opacity: 1;
  transition: opacity 0.5s ease;
  transition-delay: 1.2s;
}

.home-slide__title .title-line {
  display: block;
  overflow-y: hidden;
}

.home-slide__title .title-line span {
  display: inline-block;
  transform: translate3d(0, 140%, 0);
  opacity: 0;
  transition: transform 0.4s ease, opacity 0.8s ease;
}

.home-slide__title .title-line span:nth-child(1) {
  transition-delay: 0.15s;
}

.home-slide__title .title-line span:nth-child(2) {
  transition-delay: 0.3s;
}

.is-active .home-slide__title .title-line span {
  transform: translate3d(0, 0%, 0);
  opacity: 1;
  transition: transform 0.6s cubic-bezier(0.77, 0, 0.175, 1), opacity 0.1s ease;
}

.is-active .home-slide__title .title-line:nth-of-type(2n) span {
  transition-delay: 0.2s;
}

.home-slide__figure {
  z-index: 7;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  transition: transform 0.5s cubic-bezier(0.19, 1, 0.22, 1);
}

.home-slide__img {
  position: relative;
  display: block;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 50%;
  -webkit-backface-visibility: hidden;
  height: 0%;
  width: 100%;
  filter: grayscale(0%);
  transition: height 1s 1.4s cubic-bezier(0.19, 1, 0.22, 1), filter 0.4s 0.1s ease;
}

.is-active .home-slide__img {
  height: 100%;
  opacity: 1;
  transition: height 0.5s 0.3s cubic-bezier(0.77, 0, 0.175, 1), filter 0.4s 0.1s ease;
}

.is-sliding .home-slide__img {
  filter: grayscale(100%);
}

/*

Sliders

 */
.swiper-slider {
  position: relative;
}

.swiper-pagination {
  left: -10px;
  right: -10px;
  bottom: 10px;
  z-index: 5;
}

.swiper-pagination--static {
  position: relative;
  left: 0;
  right: 0;
  bottom: 0;
  margin-top: 35px;
  margin-bottom: -10px;
}

.swiper-pagination--static:first-child {
  margin-top: 0;
}

.lt-ie9 .swiper-pagination {
  display: none;
}

.swiper-pagination-bullet {
  position: relative;
  height: 20px;
  width: 20px;
  background: #ebebeb;
  border: 1px solid transparent;
  opacity: 1;
  margin: 10px;
}

.swiper-pagination-bullet:before {
  position: absolute;
  top: -5px;
  right: -5px;
  bottom: -5px;
  left: -5px;
  content: '';
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: #82bac3;
}

.swiper-pagination--numbers {
  left: auto;
  right: 50%;
  margin-right: -570px;
  top: 50%;
  bottom: auto;
  margin-top: -150px;
  height: 300px;
  line-height: 300px;
  display: flex;
  font-size: 0;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 1250px) {
  .swiper-pagination--numbers {
    left: 0;
    right: 0;
    margin-right: 0;
    top: auto;
    bottom: 60px;
    margin-top: 0;
    height: auto;
    line-height: 1;
    text-align: center;
    display: block;
    font-size: 0;
  }
}

@media (max-width: 780px) {
