}
.doma {
  background: linear-gradient(90deg, rgba(241,244,244,1) 0%, rgba(241,244,244,1) 20%, rgba(241,244,244,0) 55%);
  &-examples {
    position: relative;
    display: flex;
    z-index: 1;
    .doma-item {
      overflow: hidden;
      height: 400px;
      position: relative;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      background: #fff;
      img {
        // max-width: 579px;
        // height: 475px;
        max-width: 580px;
        height: 560px;
        left: -270px;
        position: absolute;
        filter: grayscale(0.9);
        transition: ease-in-out .2s;
        margin-right: -134px;
        top: 10px;
        &:hover {
          filter: none;
          transform: scale(1.1);
        }
      }
      p {
        font-size: 16px;
        font-family: "BebasNeue", sans-serif;
        line-height: 1.125;
        position: absolute;
        color: #000000;
        letter-spacing: 1px;
        font-weight: 600;
        padding: 21px 8px 0 0;
        text-align: right;
        text-transform: uppercase;
        margin-top: 0;
        right: 0;
        width: 100%;
        height: 80px;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&1+12,0+100 */
        background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 30%, rgba(255,255,255,0) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 30%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 30%,rgba(255,255,255,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 ); /* IE6-9 */

      }
    }
  }
  .about-blue-line {
    position: relative;
    background-color: #82bac38c;
    .container {
      &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 100vw;
        left: -100vw;
        background-color: #f1f4f4;
      }
      &:after {
        content: '168';
        font-family: "BebasNeueBold", sans-serif;
        position: absolute;
        left: -90px;
        top: 50%;
        font-size: 100px;
        color: #838383;
        line-height: 0;
        @media (max-width: $tablet) {
          display:none;
        }
      }
    }

  }
  &-about {
    position: relative;
    display: flex;
    justify-content: flex-start;
    padding: 50px 80px;
    p {
      max-width: 820px;
      font-size: 14px;
      line-height: 24px;
    }
    @media (max-width: 600px) {
      padding: 8%;
    }
  }
  @media(max-width: $tablet) {
    .doma-examples {
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      .doma-item {
        width: 50%;
        min-width: 240px;
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;
        img {
          max-width: 677px;
          position: initial;
          margin: 0;
        }
      }
    }
  }
  @media (max-width: 560px) {
    .container {
      padding: 0;
    }
    .doma-examples {
      flex-wrap: wrap;
      .doma-item {
        height: 165px;
        position: relative;
        width: 100%;
        display: flex;
        img {
          position: initial;
          max-width: 580px;
          height: 521px;
          transition: ease-in-out .5s;
        }
      }
    }
  }
}
