.projects {
  position: relative;
  background-color: #f1f4f4;
  .container {
    &:before {
      content: '';
      position: absolute;
      top: 45px;
      left: 0;
      height: 3px;
      width: 50px;
      background-color: #82bac3e0;
    }
    &:after {
      content: '';
      position: absolute;
      top: 70px;
      left: -24px;
      height: 3px;
      width: 50px;
      background-color: #82bac3e0;
      transform: rotate(90deg);
    }
  }
  .wrapper-all {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .wrap-foot {
      position: relative;
      display: flex;
      max-width: 955px;
      justify-content: space-between;
      margin: 10px auto;
      flex-wrap: wrap;
      margin-bottom: 90px;
      width: 100%;
      &-left {
        flex: 2 1;
        min-width: 400px;
        margin: 10px;
        position: relative;
        max-width: 500px;
        p {
          line-height: 1.5;
          margin: 0;
        }
      }
      &-right {
        position: relative;
        flex: 1 1;
        display: flex;
        justify-content: flex-end;
        margin: 10px;
        .btn {
          min-width: 299px;
          // background: #525656 ;
          // border-color: #525656;
        }
      }
      @media (max-width: $tablet) {
        align-items: center;
        flex-direction: column;
        &-left {
          min-width: initial;
          p {
            text-align: center;
          }
        }
        &-right {
          justify-content: center;
        }
      }
    }
  }
  .wrap-projects {
    position: relative;
    // display: flex;
    // flex-wrap: wrap;
    // justify-content: center;
    margin: 140px 0 15px 0;
    display: grid;
    grid-gap: 15px;
    grid-auto-rows: minmax(150px, auto);
    max-width: 1000px;
    @media (min-width: 650px) {
      grid-template-columns: 300px 300px;
    }
    @media (min-width: 920px) {
      grid-template-columns: 300px 300px 300px;
      grid-template-rows: minmax(150px, auto) minmax(150px, auto);
      grid-auto-rows: minmax(100px, auto);
    }
    a {
      text-decoration: none;
      display: flex;
      justify-content: center;
    }
    &:before {
      content: '';
      position: absolute;
      font-family: "BebasNeueBold", sans-serif;
      font-size: 32px;
      font-weight: 400;
      line-height: 1.3;
      color: #585858;
      margin: 0 0 25px 0;
      letter-spacing: 1px;
      text-transform: uppercase;
      top: -60px;
    }
    &:after {
      content: '  ';
      position: absolute;
      transform: rotate(-90deg);
      top: 102px;
      left: -230px;
      font-family: "BebasNeueBold", sans-serif;
      font-size: 48px;
      font-weight: 400;
      line-height: 1.3;
      // color: #58585836;
      color: #838383;
      opacity: 0.3;
      letter-spacing: 2px;
      text-transform: uppercase;
    }
    .project-item {
      position: relative;
      width: 300px;
      height: 480px;
      // margin: 10px;
      // cursor: pointer;
      &:hover {
        .project-item-img img {
          filter: grayscale(0);
        }
        .project-item-img {
          &:before {
            // left: -89px;
            // top: -117px;
            opacity: 1;
            transform: rotate(45deg) translateX(-150px);
          }
          &:after {
            // top: 49px;
            // left: 20px;
            // transform: translate(20px, 49px);
            // transform: rotate(-45deg) translate(20px, 49px);
            transform: rotate(-45deg) translate(-20px, 40px);
            opacity: 1;
          }
        }
      }
      &-img {
        position: relative;
        width: 300px;
        height: 300px;
        overflow: hidden;
        &:before {
          content: '';
          position: absolute;
          height: 230px;
          width: 200px;
          transform: rotate(45deg) translateX(-200px);
          // transform: rotate(45deg);
          background-color: #82bac3e0;
          z-index: 1;
          // left: -200px;
          // top: -200px;
          opacity: 0;
          transition: ease-in-out .5s;
        }
        &:after {
          content: ' > ';
          position: absolute;
          color: #ffffffad;
          // top: -200px;
          z-index: 1;
          // left: 200px;
          // transform: rotate(-45deg);
          transform: rotate(-45deg) translate(40px, 40px);
          opacity: 0;
          transition: ease-in-out .5s;
        }
        img {
          max-height: 100%;
          max-width: fit-content;
          filter: grayscale(1);
          transition: ease-in-out .5s;
          position: absolute;
        }
      }
      &-describe {
        padding: 30px;
        max-height: 180px;
        display: flex;
        flex-direction: column;
        border: 1.5px solid gray;
        border-top: 0;
        background-color: white;
        p {
          margin: 1px;
          color: #a3a2a2;
        }
        &-head {
          flex: 1;
          .head-header p{
            font-size: 24px;
            font-family: "BebasNeueBold", sans-serif;
            font-weight: 400;
            line-height: 1.3;
            color: #585858;
            letter-spacing: 1px;
            text-transform: uppercase;
            // padding: 0;
          }
          .head-subheader {
            font-size: 13px;
          }
