  &-container{
    width: 570px;
    padding-left: 20px;
    margin-bottom: 55px;
    position: relative;
    @media (max-width: $tablet) {
      padding: 0;
      margin: 0 auto 35px auto;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }

    &:before{
      content: '';
      width: 85%;
      height: 1px;
      background: #585858;
      @include position(absolute, 20px null null 10%);
      z-index: 0;
      @media (max-width: $mobile-small) {
        display: none;
      }
    }
    &:after{
      content: '';
      width: 70px;
      height: 1px;
      background: #585858;
      @include position(absolute, 16px null null 0);
      z-index: 0;
      transform: rotate(30deg);
      @media (max-width: $tablet) {
        @include position(absolute, 16px null null -20px);
      }
      @media (max-width: $mobile) {
       display: none;
      }
    }
  }
  &-item{
    width: 80px;
    text-align: center;
    position: relative;
    z-index: 5;
    @media (max-width: $mobile) {
      width: 73px;
    }
    @media (max-width: $mobile-small) {
      width: 30%;
      margin-bottom: 40px;
    }

    span{
      display: block;

      &:first-child{
        color: #585858;
        font-family: $font2;
        font-size: 32px;
        font-weight: 700;
        margin-bottom: 28px;
        width: 40px;
        height: 40px;
        background: #f4f4f4;
        border: 1px solid #585858;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        border-radius: 40px;
        padding-top: 5px;
      }
      &:last-child{
        font-family: $font2;
        font-size: 16px;
        line-height: 1.5;
        letter-spacing: 0.5px;
      }
    }

    &:nth-child(5),&:nth-child(6){
      span{
        &:first-child{
          margin-bottom: 17px;
        }
      }
    }
  }

  &-form{
    @media (max-width: $tablet) {
      margin: auto;
      margin-top: 40px;
    }
  }
}
.geo{
  padding: 100px 0 90px 0;
  position: relative;


  &:before{
    content: '';
    background: url("../img/bg8.png") no-repeat;
    width: 525px;
    height: 340px;
    @include position(absolute, -100px 0 null null);
    @media (max-width: $mobile) {
      display: none;
    }
  }

  .h2{
    padding-left: 70px;
    margin-bottom: 45px;
    @media (max-width: $mobile) {
      padding: 0 1%;
    }

    &.geo-list--title{
      font-size: 20px;
      margin-bottom: 0;
      padding: 20px 0 0 0;
    }
  }

  &-text{
    max-width: 570px;
    @media (max-width: $tablet){
      padding: 0 1%;
    }
    @media (max-width: $mobile) {
      max-width: 100%;
      padding: 0 1%;
    }
  }

  &-info{
    margin: 70px 0 75px 0;
    height: 385px;
    @media (max-width: $tablet) {
      margin: 40px 0 45px 0;
    }
    @media (max-width: $mobile) {
      height: auto;
    }
  }
  &-map{
    width: 650px;
    height: 100%;
    @media (max-width: $tablet) {
      width: 60%;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  #map{
    width: 650px;
    height: 100%;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &-list{
    width: 390px;
    height: 100%;
    background: rgba(#afa135,0.7);
    position: relative;
    padding: 0 40px;
    @media (max-width: $tablet) {
      width: 40%;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }

    >div{
      overflow-y: auto;
      padding: 30px 0;
      height: 100%;


      >div{
        margin-bottom: 16px;

        a{
          text-decoration: underline;
          font-size: 14px;
          color: #000;
          font-weight: 300;

          &.active,&:hover{
            font-weight: 700;
          }
        }
      }
    }

    &:before{
      content: '';
      @include position(absolute, 0 null null 100%);
      width: 100%;
      height: 100%;
      background: rgba(#afa135,0.7);
    }
    &:after{
      content: '';
      @include position(absolute, 0 null null -20px);
      width: 20px;
      height: 100%;
      background: rgba(#bcb15a,0.7);
    }

  }

  &-item{
    padding: 30px;
    @media (max-width: $tablet) {
      padding: 30px 1%;
    }
    &-info{
      width: 310px;
      @media (max-width: $tablet) {
        width: 35%;
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin-bottom: 30px;
      }
      &--name{
        font-size: 18px;
        font-family: $font2;
        margin-bottom: 15px;
      }
      p{
        font-size: 12px;
      }
      a{
        color: $color2;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 12px;
      }
    }
    &-more{
      width: 630px;
      @media (max-width: $tablet) {
        width: 62%;
      }
      @media (max-width: $mobile) {
        width: 100%;
      }

      &--item{
        margin-bottom: 25px;
        span{
          display: block;
          text-align: center;
          &:first-child{
            font-size: 12px;
            margin-bottom: 8px;
            font-weight: 300;
          }
          &:last-child{
            font-size: 18px;
            font-family: $font2;
          }
        }
      }

      .cols{
        &:first-child{
          border-bottom: 2px solid #000;
          margin-bottom: 23px;
        }
        &:last-child{

          @media (max-width: $mobile) {
            justify-content: space-around;
          }

          a{
            display: block;
            margin-right: 80px;
            text-decoration: none;
            text-transform: uppercase;
            font-size: 12px;
            font-weight: 300;
            @media (max-width: $mobile) {
              margin-right: 0;
            }

            .icon{
              color: #636363;
              font-size: 34px;
              margin-right: 15px;
            }
          }
        }
      }
    }
  }
}
